@import "./mixin";

.themeFC {
    color:#9b815d !important;
}
.highlight {
    position:relative;
    &::before {
        content: "";
        display:block;
        @include position(absolute, $bottom:0, $left:0);
        width: $full;
        height:10px;
        background-color: #9b815d;
        opacity:1;
        z-index:-1;
    }
}